export default {
	data() {
		return {}
	},
	onLoad:function(){
		
	},
	methods:{
		getUserInfo:function(){
			uni.getUserInfo({
			  provider: 'weixin',
			  success:(res)=>{
				 if(res.errMsg == 'getUserInfo:ok'){
					 this.gotoLogin()
				 }else{
				  uni.navigateBack({
						 delta:1
					})
				 }
			  }
			});
			
		},
		gotoLogin:function(){	
			uni.navigateTo({
				url:'/pages/login/login_type'
			})
		}
	}
}
